import React, { useState } from 'react';
import { Link, Redirect, withRouter } from 'react-router-dom';

import Match from "../../components/Match"

const axios = require('axios');

function Team({match}){
    console.log(match.params.idteam);
    const [team,setTeam] = useState(null);

    const [idteam, setIdTeam] = useState(match.params.idteam)

    if(team === null){
        axios.get(`https://api.overwatchleague.com/teams/${idteam}`)
        .then(function (response) {
            console.log(response.data)
            setTeam(response.data);
        })
        .catch(function (error) {
            console.log(error);
        });
        return (
            <div>
            <h2>Teams</h2>
            </div>
            );
        }else{

            const Matchs = team.schedule.map(match => {
                return  <Match match={match} key={match.id} />
            })
            const Players = team.players.map(player => {

                return  <Player player={player} key={player.id} />
            })

            return (
                <div className="Team">
                <h2>{team.name}</h2>
                <div className="body">
                {/* <div className="img">
                <img src={team.logo} alt=""/>
                </div> */}
                <h3>Planning</h3>
                <div className="shedule flex center">
                {Matchs}
                
                
                </div>
                <h3>Roster</h3>
                <div className="roster flex center six">
                {Players}

                </div>
                </div>
                </div>
                );
        }
}

function Player({player}){
    return(<div className="player">
    <div className="playerName">{player.name}</div>
    <div className="playerImg">
    <img src={player.headshot} alt=""/>
    </div>
        
    </div>);
}

export default withRouter(Team);