import React, { useState } from 'react';
import './assets/css/App.css';

import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import cookie from 'react-cookies'


import Weeks from "./pages/Weeks";
import Teams from "./pages/Teams";
import Login from "./pages/Login";
import SignIn from "./pages/SignIn";
import Nav from "./components/Nav";
import Team from "./pages/Team";

const axios = require('axios');

console.log(cookie.load('login'));

function App(){
  const [shedule,setShedule] = useState(null);
  
  if(shedule === null){
    axios.get('https://api.overwatchleague.com/schedule')
    .then(function (response) {
      
      setShedule(response.data);
    })
    .catch(function (error) {
      console.log(error);
    });
    return (
      <div className="App">
      </div>
      );
    }else{
      return (
        <Router>
        <div className="App">
        
        <div className="main">
        <Nav/>
        
        <Route exact path="/" render={()=>
          <h2>Home</h2>
        }/>
        <Route  path="/matches" render={()=><Weeks weeks={shedule.data.stages[0].weeks}/>}/>
        <Route  path="/teams" render={()=><Teams></Teams>}/>
        <Route  path="/login" render={()=><Login></Login>}/>
        <Route  path="/signIn" render={()=><SignIn></SignIn>}/>
        <Route  path="/team/:idteam" render={()=><Team></Team>}/>
        </div>
        
        </div>
        </Router>
        );
      }
    }
    
    
    
    export default App;
